
.footer{
    background: #000;
    color: #fff;
    position: relative;
}

.footer_box h4{
    margin: 0 0 12px 0;
    text-align: left;
}


@media screen and (min-width: 960px){
    .footer_box_line{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
    }

    .footer_box{
        width: 43%;
    }

    .footer_follows a{
        width: 40%;
    }

    .footer_follows svg{
        width: 100%;
    }

    .footer h4{
        font-size: 25px;
    }

    .footer>.container>.footer_box_line>.footer_box:nth-child(1){
        width: 55%;
    }
    
    .footer>.container>.footer_box_line>.footer_box:nth-child(2){
        width: 34%;
    }

    .footer_lang{
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        margin-top: 10px;
    }
    
    .footer_lang span{
        font-size: 18px;
        margin-right: 15px;
    }

    .footer p,
    .footer a{
        font-size: 19px;
        color: #fff;
        text-decoration: none;
        text-align: left;
        font-family: 'Montserrat-Medium';
    }

    .footer a:link,
    .footer a:visited{
        color: #fff;
    }


    .footer_contacts>a{
        margin-top: 7px;
        margin-bottom: 7px;
    }

    .footer_lang span{
        cursor: pointer;
        transition: .2s;
    }

    .footer_lang span:hover{
        opacity: .8;
    }

    .equinox_rights{
        margin-top: 1vw;
        width: 280px;
    }
}



@media screen and (max-width: 960px){
    .footer_box {
        width: 100%;
    }
    .footer_box_line{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
    }

    
    .footer_follows{
        -ms-flex-line-pack: start;
            align-content: flex-start;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
    }

    .footer_follows a{
        width: 40%;
    }

    .footer_follows svg{
        width: 100%;
    }

    .footer h4{
        font-size: 30px;
    }

    .footer>.container>.footer_box_line>.footer_box:nth-child(1){
        width: 90%;
        max-width: 420px;
    }
    
    .footer>.container>.footer_box_line>.footer_box:nth-child(2){
        width: 90%;
        max-width: 420px;
    }

    .footer_lang{
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        max-width: 60%;
        margin: 0 auto;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        margin-top: 10px;
        margin-bottom: 30px;
    }
    
    .footer_lang span{
        font-size: 16px;
        margin-right: 10px;
        margin-left: 10px;
    }

    .footer p,
    .footer a{
        font-size: 14px;
        font-family: 'Montserrat-Medium';
    }

    .footer_contacts>a{
        margin-top: 7px;
        margin-bottom: 7px;
    }

    .footer_contacts{
        margin-bottom: 20px;
    }

    .equinox_rights{
        margin-top: 1vw;
        width: 250px;
    }
}



.footer_box{
    
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}

.footer_box>p{
    text-align: left;
}


.footer_info{
    margin-top: 25px;
    margin-bottom: 20px;
}

.footer>.container>.footer_box_line>.footer_box:nth-child(2) img{
    width: 100%;
}

.footer>.container>.footer_box_line>.footer_box:nth-child(2) img:nth-child(2){
    margin-top: 20px;
}

.footer>.container>p{
    margin: 50px 0 0 0;
    width: 100%;
    text-align: center;
}

.footer>.container>a{
    width: 100%;
    text-align: center;
    justify-content: center;
    display: flex;
}