
.video_section{
    padding-top: 110px;
}

.video_container{
    max-width: 760px;
    margin: 0 auto;
}

.video_section h2{
    text-align: left;
    font-size: 40px;
    margin: 0;
}

.video_section h3{
    text-align: left;
    font-size: 30px;
    margin: 0;
}

.video_section video{
    width: 100%;
    outline: none;
}

.video_info_list{
    width: 90%;
    /* margin: 0 auto; */
    /* flex-direction: column; */
    margin-top: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    list-style-type: none;
}

.video_info_list li{
    text-align: left;
    white-space: pre-wrap;
    font-family: 'Montserrat-Medium';
    font-size: 18px;
}

.video_info_list li:not(:nth-child(4)){
    width: 23%;
}

.video_info_list li:nth-child(4){
    width: 35%;
}

@media screen and (max-width: 960px){
    .video_container{
        width: 100%;
        margin: 0 auto;
        max-width: unset;
    }

    .video_section h2{
        font-size: 8vw;
        width: 90%;
        margin: 0 auto;
    }

    .video_section h3{
        font-size: 8vw;
        width: 90%;
        margin: 0 auto;
    }

    .video_info_list {
        width: 90%;
        margin: 0 auto;
        flex-direction: column;
        margin-top: 20px;
    }

    .video_info_list li{
        font-size: 3vw;
    }

    .video_info_list li:nth-child(1){
        width: 30%;
    }

    .video_info_list li:not(:nth-child(1)){
        width: 20%;
    }
    
    .video_info_list li:nth-child(4){
        width: 30%;
    }

}