.blog_post_box{
    position: relative;
    height: 500px;
    margin-bottom: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.blog_section{
    padding-top: 100px;
}

.blog_read_more{
    position: absolute;
    bottom: -70px;
    right: 0;
    color: #fff;
    font-size: 20px;
    width: 145px;
    height: 32px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.blog_read_more:hover{
    text-decoration: underline;
}

.blog_read_more>svg{
    position: absolute;
    z-index: 0;
    width: 14px;
    right: 15px;
    top: 25px;
}

.blog_media_box{
    /* width: 30%; */
    margin-right: 25px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}

.blog_info_box{
    /* border: 1px solid red; */
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis; /* will make [...] at the end */ /* change to your preferences */
    overflow: hidden;
}

.blog_info_box *{
   text-align: left;
}

.blog_info_box h2{
    font-size: 40px;
    margin-top: 0;
}

.blog_info_box *:not(h2) {
    font-family: 'Montserrat-Medium';
    font-size: 23px;
    line-height: 25px;
}

.blog_picture_box{
    width: 190px;
    height: 190px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    overflow: hidden;
}

.blog_picture_box img{
    height: 100%;
    width: auto;
}

.date_post_blog .blog_post_day{
    position: absolute;
    color: #5C5C5C;
    opacity: 0.17;
    z-index: -1;
}

.blog_date_box{
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 190px;
    height: 160px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.blog_post_day{
    position: absolute;
    font-size: 190px;
}

.blog_post_time{
    color: #FFB100;
    font-size: 30px;
    font-family: 'Montserrat-Medium';
}

.blog_post_other{
    font-size: 34px;
}

.blog_picture_section{
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.blog_picture_box:nth-child(2){
    position: absolute;
    z-index: 1;
    top: 20%;
    right: 15%;
}

.blog_picture_box:nth-child(3){
    position: relative;
    top: -10%;
}

@media screen and (max-width: 960px){

    .blog_section{
        padding-top: 30px;
    }

    .blog_section .container{
        width: 90%;  
    }

    .blog_info_box h2{
        font-size: 4vw;
    }

    .blog_date_box{
        width: 20vw;
        height: 20vw;
    }

    .blog_post_box{
        max-height: 90vw;
        margin-bottom: 20vw; 
    }

    .blog_post_day{
        font-size: 20vw;
    }

    .blog_media_box{
        margin-right: 10px;
    }

    .blog_post_time{
        font-size: 4vw;
    }

    .blog_post_other{
        font-size: 4vw;
    }

    .blog_info_box *:not(h2) {
        font-size: 3vw;
        line-height: 5vw;
    }

    .blog_picture_box {
        width: 20vw;
        height: 20vw;
    }

    .blog_read_more {
        font-size: 4vw;
        width: 30vw;
        height: 7vw;
        bottom: -15vw;
        margin-top: 0;
    }

    .blog_read_more>svg{
        width: 3vw;
        right: 3vw;
        top: 6.5vw;
    }
}