

.support_section::after{
    position: fixed;
    content: ' ';
    top: 0;
    left: 0;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background: url('../media/support_bg.svg') no-repeat;
    background-position: center 70%;
    background-size: auto 75%;
}

.support_section{
    position: relative;
    padding-top: 85px;
}

.support_main_info_box{
    overflow: hidden;
    max-height: 70px;
    width: 77%;
}

.support_main_info_box img{
    width: 100%;
    height: auto;
}

.support_section h2{
    font-size: 62px;
    margin: 0;
}

.support_section p{
    font-family: 'Montserrat-Medium';
    font-size: 22px;
}

.support_read_more{
    position: absolute;
    bottom: 0px;
    right: 0;
    color: #fff;
    font-size: 20px;
    width: 145px;
    height: 32px;
    margin-top: 20px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.support_read_more:hover{
    text-decoration: underline;
}

.support_read_more>svg{
    position: absolute;
    z-index: 0;
    width: 13px;
    right: 15px;
    top: 27px;
}

.answer_box{
    position: relative;
    margin-top: 80px;
}

.answer_icon{
    position: absolute;
    top: 0;
    width: 30px;
    left: -45px;
}

.answer_box h3{
    font-size: 23px;
}

.answer_box p{
    position: relative;
    font-size: 23px;
    line-height: 24px;
    color: #5C5C5C;
    overflow: hidden;
}

.answer_box *{
    text-align: left;
}


@media screen and (max-width: 960px){
    .support_section h2{
        font-size: 10vw;
    }

    .support_section{
        padding-top: 40px;
    }

    .support_section .container{
        width: 90%;
    }

    .support_section p{
        font-size: 5vw;
    }

    .support_read_more{
        position: absolute;
        bottom: -10vw;
        right: 0;
        width: 40vw;
        height: 8vw;
        font-size: 5vw;
    }
    
    .support_read_more:hover{
        text-decoration: underline;
    }
    
    .support_read_more>svg{
        position: absolute;
        z-index: 0;
        width: 13px;
        right: 15px;
        top: 27px;
    }

    .answer_box{
        margin-top: 20vw;
        margin-bottom: 10vw
    }

    .support_main_info_box{
        width: 100%;
    }

    .answer_box p{
        font-size: 4vw;
        line-height: 5vw;
    }

    .answer_icon{
        position: absolute;
        top: 1vw;
        width: 8vw;
        left: 0vw;
    }
    
    .answer_box h3{
        font-size: 5vw;
        padding-left: 10vw;
    }

    .answer_box p::after{
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 5;
        content: ' ';
        width: 100%;
        -webkit-box-shadow: 0vw -.3vw 1vw 1vw rgba(255, 255, 255, 0.659);
                box-shadow: 0vw -.3vw 1vw 1vw rgba(255, 255, 255, 0.659);
    }
}