
.service_preview h2{
    color: white;
}

@media screen and (min-width: 1280px){
    .service_preview h2{
        font-size: 95px;
        margin: 90px 0 0 0;
    }
    .service_preview p{
        text-align: left;
        font-size: 22px;
    }
    .service_preview>.container{
        height: 370px;
    }

    .service_info>div>p{
        font-size: 22px;
    }
}

@media screen and (min-width: 960px) and (max-width: 1280px){
    .service_preview h2{
        font-size:  8vw;
        margin: 110px 0 0 0;
    }
    .service_preview p{
        text-align: left;
        font-size: 22px;
    }

    .service_preview>.container{
        height: 370px;
    }

    .service_info>div>p{
        font-size: 22px;
    }
}

@media screen and (max-width: 960px){
    .service_preview h2{
        font-size: 9vw;
        margin: 0 0 20px 0;
    }
    .service_preview p{
        text-align: center;
        width: 90%;
        margin: auto;
        font-size: 4vw;
    }
    .service_preview>.container{
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        padding-top: 30vw;
        padding-bottom: 15vw;
    }

    .service_info>div>p{
        font-size: 4vw;
    }
}

.service_preview p{
    margin: 0 auto;
    color: #fff;
}

.service_page p{
    font-family: 'Montserrat-Medium';
}

.service_info>div>p{
    text-align: left;
    margin-top: 0px;
}

.service_preview{
    background: url('../media/service_bg.jpg') no-repeat;
    background-size: cover;
}

.service_preview>.container{
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}