
.budget_preview{
    background: url('../media/budget_bg.jpg') no-repeat;
    background-size: cover;
}

.budget_preview{
    color: #fff;
}

.budget_preview h2{
    font-size: 70px;
    margin: 130px 0 0 0;
}

.budget_preview p{
    font-size: 22px;
    text-align: left;
    margin-top: 0px;
    margin: 0;
}

.budget_preview>div>p{
    font-family: 'Montserrat-Medium';
}

.budget_description{
    padding: 50px 0 20px 0;
}

.budget_preview ul{
    font-size: 22px;
    text-align: left;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.budget_preview li{
    font-weight: normal;
    font-style: normal;
    font-variant: normal;
    text-decoration: none;
    font-family: 'Montserrat-Medium';
}

.budget_contact_box{
    margin-top: 50px;
    margin-bottom: 50px;
}

.budget_contact_us{
    position: relative;
    font-family: 'Montserrat-Black';
    padding: 2px 5px 5px 5px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 5px;
    text-decoration: underline;
    cursor: pointer;
}

.budget_contact_us:hover{
    text-decoration: none;
}

.budget_contact_us svg{
    position: absolute;
    width: 15px;
    top: 28px;
    right: 10px;
}

@media screen and (max-width: 960px){
    .budget_preview h2{
        font-size: 10vw;
        margin: 50px 0 0 0;
    }
    
    .budget_preview p{
        font-size: 4vw;
    }

    .budget_preview .container{
        width: 90%;
    }

    .budget_preview ul{
        font-size: 4vw;
    }


    .budget_contact_us{
        padding: 1vw 5px 1vw 5px;
    }
    
    .budget_contact_us svg{
        width: 2vw;
        top: 6.5vw;
    }

    .budget_contact_box{
        margin-top: 40px;
        margin-bottom: 0px;
    }
}