.form_radio_box {
    display: block;
    position: relative;
    padding-left: 2.5vw;
    margin-bottom: 1vw;
    cursor: pointer;
    font-size: 1.1vw;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

.form_radio_box input{
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 1.4vw;
    width: 1.4vw;
    border-radius: .5vw;
    border: .1vw solid #C7C7C7;
    background-color: #fff;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
  }

  /* On mouse-over, add a grey background color */
.form_radio_box:hover input ~ .checkmark {
    background-color: rgba(204, 204, 204, 0.44);
  }
  
  /* When the checkbox is checked, add a blue background */
  .form_radio_box input:checked ~ .checkmark {
    background-color: #FFB200;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .form_radio_box .checkmark::after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .form_radio_box input:checked ~ .checkmark::after {
    display: block;
  }

  .form_radio_box .checkmark::after {
    left: .45vw;
    top: .2vw;
    width: .3vw;
    height: .7vw;
    border: solid white;
    border-width: 0 .2vw .2vw 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .form_file_box{
      position: relative;
      padding-left: 3vw;
      cursor: pointer;
  }

  .form_file_box svg{
      left: 0;
      top: -.7vw;
      width: 2.5vw;
      position: absolute;
  }

  .form_file_box input{
      display: none;
  }

