.blog_post_view{
    background: #000;
    color: #fff;
}

.blog_post_section{
    padding-top: 130px;
}


.date_post_view{
    position: fixed;
    z-index: 1;
    top: 100px;
    left: 100px;
}

.date_post_view .blog_post_day{
    color: #fff;
    position: absolute;
    left: 0px;
    opacity: 0.4;
    font-size:  170px;
    z-index: -1;
}

.post_view_container{
    margin: 0 auto;
    max-width: 700px;
    position: relative;
}

.blog_post_view_info_box{
    text-align: left;
}

.blog_post_view_info_box img{
    width: 100%;
}

.blog_post_view_info_box h1{
    font-size: 45px;
    line-height: 50px;
    margin: 0;
}

.blog_post_view_info_box h2{
    font-size: 40px;
    line-height: 45px;
    margin: 0;
}

.blog_post_view_info_box h3{
    font-size: 35px;
    line-height: 40px;
    margin: 0;
}

.blog_post_view_info_box h4{
    font-size: 30px;
    line-height: 35px;
    margin: 0;
}

.blog_post_view_info_box h5{
    font-size: 25px;
    line-height: 30px;
    margin: 0;
}

.blog_post_view_info_box h6{
    font-size: 20px;
    line-height: 25px;
    margin: 0;
}


.blog_post_view_info_box ul li,
.blog_post_view_info_box ol li,
p
{
    font-size: 18px;
    font-family: 'Montserrat-Medium';
}



.post_button{
    background: #fff;
    font-size: 20px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.blog_post_exit_button{
    position: fixed;
    top: 100px;
    right: 50px;
    width: 100px;
    height: 32px;
    margin-top: 20px;
    text-decoration: none;
    color: #fff;
}

.blog_post_exit_button:visited{
    color: #fff;
}

.location_post_link_button{
    position: relative;
    margin: 0 auto;
    width: 70px;
    height: 30px;
    margin-top: 40px;
    margin-bottom: 20px;
}

.post_button:hover span{
    text-decoration: underline;
}

.post_button>svg{
    position: absolute;
    z-index: 1;
}

.location_post_link_button>svg{
    width: 10px;
    right: 10px;
    top: 30px;
}

.blog_post_exit_button>svg{
    width: 12px;
    right: 10px;
    top: 27px;
}

@media screen and (max-width: 1300px){
    .date_post_view .blog_post_day{
        color: #fff;
        opacity: 0.4;
        font-size:  100px;
        z-index: -1;
    }

    .blog_post_view .blog_date_box{
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        width: 100px;
        height: 100px;
    }

    .date_post_view{
        position: fixed;
        z-index: 1;
        top: 100px;
        left: 50px;
    }

    .date_post_view .blog_post_other{
        font-size: 20px;
    }

    .blog_post_time{
        font-size: 20px;
    }
}

@media screen and (max-width: 960px){

    .blog_post_section{
        padding-top: 100px;
    }

    .date_post_view{
        display: none;
    }

    .blog_post_exit_button{
        position: fixed;
        top: 80vh;
        right: 5vw;
        z-index: 10;
    }

    .post_view_container{
        width: 90vw;
        margin: 0 auto;
    }

    .blog_post_view_info_box{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
    }

    .blog_post_view_info_box img{
        width: 100vw;
        left: -5vw;
    }

    .blog_post_view_info_box h1{
        font-size: 10vw;
        line-height: 10vw;
    }
    
    .blog_post_view_info_box h2{
        font-size: 9vw;
        line-height: 9vw;
    }
    
    .blog_post_view_info_box h3{
        font-size: 8vw;
        line-height: 8vw;
    }
    
    .blog_post_view_info_box h4{
        font-size: 7vw;
        line-height: 7vw;
    }
    
    .blog_post_view_info_box h5{
        font-size: 6vw;
        line-height: 6vw;
    }
    
    .blog_post_view_info_box h6{
        font-size: 5vw;
        line-height: 5vw;
    }
    
    .blog_post_view_info_box ul,
    .blog_post_view_info_box ol{
        padding-left: 5vw;
    }
    
    .blog_post_view_info_box ul li,
    .blog_post_view_info_box ol li,
    p
    {
        font-size: 4vw;
        font-family: 'Montserrat-Medium';
    }
}