.main_page h3{
    font-size: 38px;
    text-align: left;
    margin: 0;
}

.main_page p{
    font-family: 'Montserrat-Medium';
}

.main_services>div>p{
    font-size: 22px;
    text-align: left;
    margin-top: 20px;
}

.marvelous>.container, .price >.container{
    min-height: 470px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
}

.main_services>.container{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    padding-bottom: 110px;
    padding-top: 0;
}

.marvelous>div>p{
    width: 80%;
    font-size: 22px;
    text-align: left;
    margin-top: 20px;
}

.price>div>p{
    width: 80%;
    font-size: 22px;
    text-align: left;
    margin-top: 20px;
    margin-bottom: 50px;
}

.read_more{
    position: absolute;
    bottom: 50px;
    color: #fff;
    width: 145px;
    height: 32px;
    margin-top: 20px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.read_more:hover span{
    text-decoration: underline;
}

.read_more>svg{
    position: absolute;
    z-index: 0;
    width: 18px;
    right: 10px;
    top: 25px;
}

.marvelous{
    color: white;
    background: url('../media/main_background.jpg') no-repeat;
    background-size: cover;
}

.price>.container{
    background: url('../media/price_background.png') no-repeat;
    background-size: 500px;
    background-position: right 0px top 140px;
}

@media screen and (min-width: 960px){
    

}

@media screen and (max-width: 960px){
    .main_services>.container{
        width: 90%;
    }

    .marvelous>.container, .price >.container{
        width: 90%;
        padding-bottom: 90px;
    }

    .price>div>p{
        width: 100%;
    }

    .marvelous>div>p{
        width: 100%;
    }

    .price>.container{
        background-size: 90% auto;
        background-position: center;
    }
}