

.header{
    position: fixed;
    width: 100vw;
    z-index: 100;
    top: 0;
    background: #fff;
}

.header_container{
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    max-width: 1200px;
}


@media screen and (min-width: 1280px){
    .header_container{
        padding: 7px 0 7px 0;
    }

    .header_nav_box{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }

    .active::before{
        width: 100%;
    }

    .header_nav_box li:hover::before{
        width: 100%;
     }

    .header_burger{
        display: none;
    }

    .header_logo_box{
        width: 100px;
    }

    .header_nav_box li{
        font-size: 16px;
    }
}

@media screen and (min-width: 960px) and (max-width: 1280px){
    .header_container{
        padding: 7px 50px 7px 50px;
    }

    .active::before{
        width: 100%;
    }


    .header_nav_box{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }

    .header_nav_box li:hover::before{
        width: 100%;
     }

     .header_burger{
        display: none;
    }

    .header_logo_box{
        width: 100px;
    }
    .header_nav_box li{
        font-size: 16px;
    }
}

@media screen and (max-width: 960px){
    .header_container{
        z-index: 10;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        height: 60px;
        /* padding-top: 10vh;
        padding-bottom: 10vh; */
    }

    .header_nav_box{
        list-style: none;
        padding: 0;
        width: 100%;
        justify-content: center;
        align-items: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        display: none;
        width: 100%;
        height: 100%;
    }

    .header_nav_box li{
        margin-bottom: 10px;
    }

    .header_nav_box li::before{
        content: '';
        position: absolute;
        bottom: -5px;
        left: 30%;
        height: 3px;
        border-radius: 3px;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
        background: #FFB100;
    }

    .active::before{
        width: 40%;
    }

    .header_nav_box li:hover::before{
        width: 40%;
     }

     .header_burger{
         display: -webkit-box;
         display: -ms-flexbox;
         display: flex;
     }

     .header_logo_box{
        position: absolute;
        left: 10px;
        top: 10px;
        width: 50px;
    }

    .header_lang_box{
        display: none;
    }

    .header_nav_box li{
        font-size: 30px;
    }

    .service_info .container{
        width: 90%;
    }

}

.inactive::before{
    width: 0%;
}

.header_nav_box{
    list-style: none;
    padding: 0;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.header_nav_box a{
    text-decoration: none;
    color: #000;
}

.header_nav_box li{
    position: relative;
    font-weight: 900;
    cursor: pointer;
    margin-left: 1.5vw;
    margin-right: 1.5vw;
}


.header_nav_box li::before{
    content: '';
    position: absolute;
    bottom: -5px;
    height: 3px;
    border-radius: 3px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    background: #FFB100;
}



.header_logo_box img{
    width: 100%;
}

.header_lang_box span{
    color: #5C5C5C;
    font-weight: 900;
    font-size: 23px;
    margin-left: 10px;
    cursor: pointer;
}

.header_lang_box span:hover{
    opacity: 0.8;
}

.header_burger{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 40px;
    cursor: pointer;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.header_burger span{
    position: relative;
    /* transform: rotate(45deg); */
    width: 100%;
    height: 5px;
    background: #000;
    border-radius: 4px;
}   

.header_burger::after{
    position: absolute;
    content: ' ';
    width: 100%;
    height: 5px;
    bottom: 0px;
    left: 0px;
    background: #000;
    border-radius: 4px;
}

.header_burger::before{
    position: absolute;
    content: ' ';
    width: 100%;
    height: 5px;
    top: 0px;
    left: 0px;
    background: #000;
    border-radius: 4px;
}