
 .slider_image_box{ 
    position: relative; 
    width: 100%; 
    display: -webkit-box; 
    display: -ms-flexbox; 
    display: flex; 
    -webkit-box-pack: center; 
        -ms-flex-pack: center; 
            justify-content: center; 
    overflow: hidden; 
    height: 900px; 
  } 
   
  .slider_image_box img{ 
    height: 100%; 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content; 
    margin-top: -90px; 
  }

.slider_text_box{
  position: absolute;
  width: 100vw;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.slider_text_box h2{
    max-width: 1200px;
    color: white;
    font-size: 90px;
    margin: 0;
}

.slider_navigation{
    position: fixed;
    z-index: 101;
    width: 100vw;
    overflow: hidden;
    top: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.slider_navigation ul{
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 0;
    border-radius: 10px;
}

.slider_navigation>ul>span{
  width: 15px;
  height: 15px;
  border: 2px solid black;
  border-radius: 5px;
  margin: 5px;
  cursor: pointer;
}

@media screen and (max-width: 960px){
  .slider_navigation{
    display: none;
  }

  
  .slider_text_box h2{
    max-width: 1200px;
    padding-left: 5%;
    padding-right: 5%;
    color: white;
    font-size: 6.5vw;
    margin: 0;
  }

  .slider_image_box {
    margin-top: 60px;
    position: relative;
    width: 100vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: 400px;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
    /* border: 1px solid red; */
}

  .slider_image_box img {
    width: unset;
    margin-top: 0px;
    height: 100%;
  }
}