

.work_section{
    /* height: 800px; */
    background: url('../media/work_bg.png');
    background-size: cover;
    padding-top: 100px;
}

.work_section h2{
    font-size: 98px;
    margin: 0;
    color: #fff;
}

.work_section p{
    font-size: 25px;
    color: #fff;
    margin-top: 0;
    font-family: 'Montserrat-Medium';
}

.work_type_button{
    position: relative;
    color: #fff;
    font-size: 20px;
    width: 115px;
    height: 35px;
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.work_type_button:hover span{
    text-decoration: underline;
}

.work_type_button svg{
    width: 13px;
    position: absolute;
    bottom: -17px;
}

.work_type_button:nth-child(2) svg{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    right: 70%;
}

.work_type_button:nth-child(1) svg{
    right: 30%;
}
.work_type_button:nth-child(3) svg{
    right: 10%;
}

.work_type_list{
    margin-top: 135px;
    margin-bottom: 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.work_select_category{
    width: 100%;
    color: #5C5C5C;
    font-size: 18px;
    font-family: 'Montserrat-Medium';
}

.projects_list{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.project_box{
    width: 49.5%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 30px;
}

.project_box h3{
    text-align: left;
    font-size: 40px;
    width: 90%;
    margin: 0;
}

.project_box>span{
    width: 90%;
    text-align: left;
    padding-left: 10px;
    font-size: 24px;
}

.project_image_box{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.project_image_box:hover{
    cursor: pointer;
}

.project_image_box img{
    width: 100%;
}

.project_info_box{
    padding: 0;
    list-style-type: none;
    text-align: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.project_info_box>li>span{
    font-size: 20px;
    padding: 0;
}

.project_info_box li{
    width: 90%;
    font-family: 'Montserrat-Medium';
}



.project_view_button{
    position: relative;
    background: #000;
    color: #fff;
    font-size: 20px;
    width: 180px;
    height: 30px;
    margin-top: 10px;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.project_view_button:hover span{
    text-decoration: underline;
}

.project_view_button svg{
    width: 13px;
    position: absolute;
    right: 15px;
    bottom: -17px;
}

@media screen and (max-width: 960px){
    .work_section{
        padding-top: 50px;
    }
    
    .work_section h2{
        font-size: 10vw;
    }
    
    .work_section p{
        font-size: 5vw;
    }

    .work_section .container{
        width: 90%;
    }

    .work_type_list{
        margin-top: 20px;
        margin-bottom: 40px;
    }
    
    .work_select_category{
        width: 100%;
        color: rgb(202, 202, 202);
        font-size: 4vw;
    }

    .work_type_button{
        font-size: 5vw;
        height: 8vw;
        width: 25%;
    }
 
    .work_type_button svg{
        width: 2vw;
        position: absolute;
        right: 15px;
        bottom: -2vw;
    }

    .project_box{
        width: 100%;
        margin-bottom: 10vw;
    }

    .project_box h3{
        width: 90%;
        text-align: left;
        font-size: 8vw;
    }

}