
.locations_preview{
    background: url('../media/locations_bg.jpg') no-repeat;
    background-size: cover;
}

.locations_preview h2{
    color: white;
    font-size: 95px;
    margin: 120px 0 0 0;
}

.locations_preview .container{
    padding-bottom: 250px;
}

.locations_preview p{
    font-size: 22px;
    text-align: left;
    top: 0px;
    color: #fff;
    font-family: 'Montserrat-Medium';
}

.locations_info>div>p{
    font-family: 'Montserrat-Medium';
}

.locations_page h3{
    font-size: 38px;
    text-align: left;
    margin: 0;
}

.locations_info p{
    font-size: 22px;
    text-align: left;
    margin-top: 20px;
}

.locations_info{
    background: url('../media/headphones.jpg') no-repeat;
    background-size: cover;
}

.locations_line_box{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.locations_line_box li{
    text-align: left;
    font-family: 'Montserrat-Medium';
    font-size: 22px;
}

.gallery_logo_box{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 22px;
}

.gallery_logo_box img{
    width: 65px;
    margin-right: 20px;
}

.location_box{
    position: relative;
    width: 310px;
    height: 310px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    overflow: hidden;
    margin-bottom: 10px;
}

.location_box:hover {
    cursor: pointer;
}

.location_box img{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-height: 200%;
    max-width: 200%;
}

.locations_gallery_box{
    margin-top: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;   
}



.gallery_box_description{


    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    position: absolute;
    width: 100%;
    height: 100%;
    background: #FFB100;
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
}

.locationPost {
    width: 100%;
    align-items: center;
    justify-content: center
}

.locationPost p {
    text-align: center !important;
    margin: 0 !important;
    width: 100% !important;
}

.gallery_box_description, .gallery_read_more {
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
}

.gallery_read_more{
    position: absolute;
    padding: 7px 2.5% 7px 2.5%;
    height: 20px;
    bottom: 20px;
    border-radius: 5px;
    width: 44%;
    left: 26.5%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
            display: flex;
            align-items: center;
            position: absolute;
            justify-content: center;
        
}

.gallery_read_more span{
    font-size: 22px;
    cursor: pointer;
}

.gallery_box_description img{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
}

.gallery_box_description p {
    width: 80%;
    height: fit-content;
    text-align: center;
    margin: 0;
    font-size: 18px;
    overflow: hidden;
    font-family: Montserrat-Medium;
}

.gallery_read_more svg{
    position: absolute;
    width: 10px;
    left: 14px;
    top: 30px;
}

.location_gallery_line{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-top: 50px;
}

.gallery_read_more span:hover{
    text-decoration: underline;
}

.location_action_button{
    position: relative;
    background: #000;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 22px;
    padding: 2.5px 10px 2.5px 10px;
    border-radius: 5px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0px 25px 0px 25px; 
    cursor: pointer;
}

.location_action_button span:hover{
    text-decoration: underline;
}

.location_action_button svg{
    position: absolute;
    width: 14px;
    top: 28px;
}

.location_action_button:nth-child(1) svg{
    right: 32%;
}

.location_action_button:nth-child(2) svg{
    right: 10%;
}


.locations_contact_box{
    margin-top: 50px;
    margin-bottom: 50px;
}

.locations_contact_us{
    position: relative;
    font-family: 'Montserrat-Black';
    padding: 2px 5px 5px 5px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 5px;
    text-decoration: underline;
    cursor: pointer;
}

.locations_contact_us:hover{
    text-decoration: none;
}

.locations_contact_us svg{
    position: absolute;
    width: 15px;
    top: 28px;
    right: 10px;
}


@media screen and (max-width: 960px){
    .locations_preview h2{
        font-size: 8vw;
        margin: 0px 0 0 0;
    }

    .locations_page h3{
        font-size: 7vw;
    }

    .locations_preview p{
        font-size: 4vw;
    }

    .locations_preview .container{
        padding-top: 90px;
        padding-bottom: 30px;
    }

    .locations_page .container{
        width: 90%;
    }

    .locations_info p{
        font-size: 4vw;
    }

    .locations_line_box li{
        font-size: 4vw;
    }


    .locations_contact_box{
        margin-top: 0px;
        margin-bottom: 0px;
    }

    .locations_contact_us{
        padding: 0.5vw 5px 0.5vw 5px;
    }

    
    .locations_contact_us svg{
        width: 3vw;
        top: 5.5vw;
    }

    .location_box{
        position: relative;
        width: 90vw;
        height: 90vw;
    }

    .location_box:hover {
        cursor: pointer;
    }

    .gallery_read_more span{
        font-size: 5vw;
    }

    .gallery_box_description p{
        font-size: 4vw;
    }

    .location_action_button{
        font-size: 4vw;
        margin: 0px 1vw 0px 1vw;
    }

    .location_action_button svg{
        position: absolute;
        width: 3vw;
        top: 5.5vw;
    }

    .location_gallery_line{
        margin-top: 40px;
    }
    
    .location_action_button:nth-child(1) svg{
        right: 15%;
    }
    
    .location_action_button:nth-child(2) svg{
        right: 10%;
    }
}

@media screen and (min-width: 600px) and (max-width: 960px){
    .location_box{
        position: relative;
        width: 29vw;
        height: 29vw;
    }

    
    .gallery_box_description p{
        font-size: 1.5vw;
    }

    .gallery_read_more span{
        font-size: 2vw;
    }

    .location_action_button{
        font-size: 3vw;
        margin: 0px 3vw 0px 3vw;
    }

    .location_action_button svg{
        position: absolute;
        width: 1.5vw;
        top: 4vw;
    }

    .location_gallery_line{
        margin-top: 40px;
    }
    
    .location_action_button:nth-child(1) svg{
        right: 15%;
    }
    
    .location_action_button:nth-child(2) svg{
        right: 10%;
    }
}