.service_box{
    text-align: center;
    position: relative;
    width: 20%;
    margin-top: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
}

.service_box svg{
    max-width: 58%;
    height: 100px;
}

.service_info_box{
    position: absolute;
    overflow: hidden;
    z-index: 1;
    border-left: 5px solid #FFB100;
    left: 0;
    top: 100%;
    width: 0px;
    /* height: 0px; */
    background: rgba(92, 92, 92, 0.86)
}

.service_box>p{
    font-size: 18px;
    white-space: nowrap;
}

.services_bar{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.service_info_box>p{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 18px;
    color: #fff;
    text-align: left;
    margin: 20px 0px 20px 10px;
    white-space: pre-wrap;
}


@media screen and (min-width: 600px) and (max-width: 960px){
    .service_box{
        width: 50%;
    }

    .service_box svg{
        width: 100%;
    }
}

@media screen and (max-width: 600px){
    .service_box{
        width: 80vw;
        margin: 10vw auto;
    }
    .service_box svg{
        max-width: 90%;
        width: 80vw;
    }

    .service_info_box{
        left: -5vw;
    }

    .service_info_box>p{
        font-size: 14px;
        max-width: 80vw;
    }
}

